import Link from "next/link";

export default function AssetLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <div className="flex h-screen">
      {/* Фиксированное меню слева */}
      <nav className="w-1/4 p-4 bg-gray-800 text-white h-full">
        <ul className="space-y-2">
          <li>
            <Link href="/asset/spot">Spot</Link>
          </li>
          <li>
            <Link href="/asset/futures">Futures</Link>
          </li>
          <li>
            <Link href="/asset/margin">Margin</Link>
          </li>
          <li>
            <Link href="/asset/copy-trading">Copy Trading</Link>
          </li>
        </ul>
      </nav>

      {/* Динамический контент справа */}
      <div className="w-3/4 p-4">{children}</div>
    </div>
  );
}
